import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    currentlistinfo: [],
    currentactpageindex: 0,
    olderangelinfo: {
      name: "",
      skill: "",
      intro: "",
      pic: "",
    },
    actinfo: {
      actname: "",
      actpic: "",
      date: "",
      time: "",
      loc: "",
      isprivate: "否",
      type: "",
      quota: "",
      deadlinedate: "",
      tag: "",
      signupinfo: "",
      describe: "",
      isolderangelevent: "否",
      fee: "否",
      price: 0,
      chargemethods: "",
      accountinfo: "",
      hostid: "",
      id: "",
    },
    editeventinfo: [],
    editolderangelinfo: [],
    userinfo: [],
    liffurlshow: false,
    editmode: false,
    navnumber: 0,
  },
  mutations: {
    addcreateactindex(state) {
      state.currentactpageindex++;
    },
    setnavnumber(state, payload) {
      state.navnumber = payload;
    },
    minuscreateactindex(state) {
      if (state.currentactpageindex == 0) {
        state.currentactpageindex = 0;
      }
      state.currentactpageindex--;
    },
    showliffurlblock(state) {
      state.liffurlshow = true;
    },
    closeliffurlblock(state) {
      state.liffurlshow = false;
    },
    setactinfo(state, payload) {
      switch (payload.datatype) {
        case "isprivate":
          state.actinfo.isprivate = payload.data;
          break;
        case "actpic":
          state.actinfo.actpic = payload.data;
          break;
        case "type":
          state.actinfo.type = payload.data;
          break;
        case "actname":
          state.actinfo.actname = payload.data;
          break;
        case "date":
          state.actinfo.date = payload.data;
          break;
        case "time":
          state.actinfo.time = payload.data;
          break;
        case "loc":
          state.actinfo.loc = payload.data;
          break;
        case "quota":
          state.actinfo.quota = payload.data;
          break;
        case "deadlinedate":
          state.actinfo.deadlinedate = payload.data;
          break;
        case "tag":
          state.actinfo.tag = payload.data;
          break;
        case "signupinfo":
          state.actinfo.signupinfo = payload.data;
          break;
        case "describe":
          state.actinfo.describe = payload.data;
          break;
        case "fee":
          state.actinfo.fee = payload.data;
          break;
        case "price":
          state.actinfo.price = payload.data;
          break;
        case "chargemethods":
          state.actinfo.chargemethods = payload.data;
          break;
        case "accountinfo":
          state.actinfo.accountinfo = payload.data;
          break;
        case "isolderangelevent":
          state.actinfo.isolderangelevent = payload.data;
          break;
      }
    },
    _uuid(state) {
      var d = Date.now();
      if (
        typeof performance !== "undefined" &&
        typeof performance.now === "function"
      ) {
        d += performance.now(); //use high-precision timer if available
      }
      state.actinfo.id = "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
        /[xy]/g,
        function(c) {
          var r = (d + Math.random() * 16) % 16 | 0;
          d = Math.floor(d / 16);
          return (c === "x" ? r : (r & 0x3) | 0x8).toString(16);
        }
      );
    },
    setuserinfo(state, payload) {
      state.userinfo = payload;
      state.actinfo.hostid = payload[3];
    },
    resetactinfo(state, payload) {
      state.currentactpageindex = payload;
    },
    setediteventinfo(state, payload) {
      state.editeventinfo = payload;
    },
    seteditmode(state, payload) {
      state.editmode = payload;
    },
    setolderangelinfo(state, payload) {
      switch (payload.datatype) {
        case "olderangelname":
          state.olderangelinfo.name = payload.data;
          break;
        case "olderangelpic":
          state.olderangelinfo.pic = payload.data;
          break;
        case "olderangelskill":
          state.olderangelinfo.skill = payload.data;
          break;
        case "olderangeldescribe":
          state.olderangelinfo.intro = payload.data;
          break;
      }
    },
    setediteditolderangelinfo(state, payload) {
      state.editolderangelinfo = payload;
    },
  },
  getters: {
    geteditolderangelinfo: (state) => {
      return state.editolderangelinfo;
    },
  },
  actions: {
    postolderangelinfo(context) {
      axios
        .get(
          "https://script.google.com/macros/s/AKfycbzyetit5OgE2YfkplHLshUykPsKXUjxm__mwxu8MIBsvDXbwmKIG8bXVPHcO2WNF-_X_w/exec",
          {
            params: {
              name: context.state.olderangelinfo.name,
              skill: context.state.olderangelinfo.skill,
              intro: context.state.olderangelinfo.intro,
              pic: context.state.olderangelinfo.pic,
              olderangelid: context.state.actinfo.id,
            },
          }
        )
        .then((res) => {
          console.table(res.data);
        })
        .catch((error) => {
          console.error(error);
        });
    },
    postactinfo(context) {
      context.commit("_uuid");
      axios
        .get(
          "https://script.google.com/macros/s/AKfycbyLSOA3S8Ht7-54hS559oYLcFJpl9cM7YR09nA2STPFASNjSxFAcaka_P8EhBeSihj7QQ/exec",
          {
            params: {
              actname: context.state.actinfo.actname,
              actpic: context.state.actinfo.actpic,
              actdate: context.state.actinfo.date,
              acttime: context.state.actinfo.time,
              actloc: context.state.actinfo.loc,
              isprivate: context.state.actinfo.isprivate,
              type: context.state.actinfo.type,
              actin: context.state.actinfo.quota,
              deadlinedate: context.state.actinfo.deadlinedate,
              tag: context.state.actinfo.tag,
              signupinfo: context.state.actinfo.signupinfo,
              describe: context.state.actinfo.describe,
              fee: context.state.actinfo.fee,
              price: context.state.actinfo.price,
              chargemethods: context.state.actinfo.chargemethods,
              accountinfo: context.state.actinfo.accountinfo,
              isolderangelevent: context.state.actinfo.isolderangelevent,
              hostid: context.state.actinfo.hostid,
              id: context.state.actinfo.id,
            },
          }
        )
        .then((res) => {
          console.table(res.data);
        })
        .catch((error) => {
          console.error(error);
        });
    },
    editactinfo(context) {
      axios
        .get(
          "https://script.google.com/macros/s/AKfycbxa71xyLi1nnwJyk-wRwd83PJzVuJsys-WKuhipbvYuRfYsMB8SNt-TXimxcYfq7Ten/exec",
          {
            params: {
              actname: context.state.actinfo.actname,
              actpic: context.state.actinfo.actpic,
              actdate: context.state.actinfo.date,
              acttime: context.state.actinfo.time,
              actloc: context.state.actinfo.loc,
              isprivate: context.state.actinfo.isprivate,
              type: context.state.actinfo.type,
              actin: context.state.actinfo.quota,
              deadlinedate: context.state.actinfo.deadlinedate,
              tag: context.state.actinfo.tag,
              signupinfo: context.state.actinfo.signupinfo,
              describe: context.state.actinfo.describe,
              fee: context.state.actinfo.fee,
              price: context.state.actinfo.price,
              chargemethods: context.state.actinfo.chargemethods,
              isolderangelevent: context.state.actinfo.isolderangelevent,
              accountinfo: context.state.actinfo.accountinfo,
              id: context.state.editeventinfo[18],
            },
          }
        )
        .then((res) => {
          console.table(res.data);
        })
        .catch((error) => {
          console.error(error);
        });
    },
  },
  modules: {},
});
