<template>
  <div class="olderangel">
    <div class="olderangelpic-wrap" v-if="test2[0]&&this.$store.state.editeventinfo[16] == '是'">
      <inputoutline
        :alreadydata="test2[0][3]"
        :placeholder="picurl"
        @inputtexthandler="catchinputcontent"
        headername="大使個人照"
      ></inputoutline>
    </div>
    <div class="olderangelpic-wrap" v-if="this.$store.state.editeventinfo==''">
      <inputoutline
        :placeholder="picurl"
        @inputtexthandler="catchinputcontent"
        headername="大使個人照"
      ></inputoutline>
    </div>
    <div class="olderangelpic-wrap">
      <form id="olderangelform">
        <input name="file" id="uploadfile" type="file" />
        <input name="filename" id="filename" type="text" placeholder="檔案名稱" />
        <input id="submit" type="submit" />
      </form>
    </div>

    <div class="olderangelname-wrap" v-if="test2[0]&&this.$store.state.editeventinfo[16] == '是'">
      <inputoutline
        v-if="test2[0]"
        :alreadydata="test2[0][0]"
        placeholder="輸入銀髮大使姓名"
        @inputtexthandler="catchinputcontent"
        headername="銀髮大使姓名"
      ></inputoutline>
    </div>
    <div class="olderangelname-wrap" v-if="this.$store.state.editeventinfo==''">
      <inputoutline
      
        placeholder="輸入銀髮大使姓名"
        @inputtexthandler="catchinputcontent"
        headername="銀髮大使姓名"
      ></inputoutline>
    </div>
    <div class="olderangelskill-wrap" v-if="test2[0]&&this.$store.state.editeventinfo[16] == '是'">
      <inputoutline
        v-if="test2[0]"
        :alreadydata="test2[0][1]"
        placeholder="輸入大使興趣或專長"
        @inputtexthandler="catchinputcontent"
        headername="銀髮大使興趣/專長"
      ></inputoutline>
    </div>
    <div class="olderangelskill-wrap" v-if="this.$store.state.editeventinfo==''">
      <inputoutline
       
        placeholder="輸入大使興趣或專長"
        @inputtexthandler="catchinputcontent"
        headername="銀髮大使興趣/專長"
      ></inputoutline>
    </div>
    <div class="olderangel-intro">
      <h4>銀髮大使簡單介紹（150字以內）</h4>
      <textarea v-model="content" maxlength="150" ref="olderangelintro"></textarea>
      <div class="wordlength" v-if="content">
        <span>{{content.length}} / 150：字數</span>
      </div>
    </div>

    <div class="basicinfo-btnwrap">
      <fillbtn
        btnwidth="193"
        btnheight="57"
        btnfz="24"
        btnname="取消"
        @clickhandler="upstep"
        class="cancelbtn"
      ></fillbtn>
      <fillbtn @clickhandler="nextstep" btnwidth="193" btnheight="57" btnfz="24" btnname="下一步"></fillbtn>
    </div>
  </div>
</template>

<script>
import fillbtn from "../components/Fillbtn";
import inputoutline from "../components/Inputoutline";
import axios from "axios";

export default {
  data() {
    return {
      checklist: [],
      content: "",
      picurl: "",
      test2: [],
      //   htmlcontent: "",
      configs: {
        autofocus: false,
        status: false,
        spellChecker: false,
        renderingConfig: {
          codeSyntaxHighlighting: true,
          highlightingTheme: "atom-one-light"
        },
        lineWrapping: true,
        placeholder: "在此輸入活動描述"
      }
    };
  },
  mounted() {
    if (this.$store.state.editeventinfo[16] == "是") {
      axios({
        method: "get",
        url:
          "https://script.google.com/macros/s/AKfycbxGkIBwYN29d0h-Pf-lTSKe_BvRCt-Eta3pDYdT-A_Us6n3L5QLFmfXYIL2Hja0d5uj/exec",
        params: {
          eventid: this.$store.state.editeventinfo[18]
        }
      }).then(response => {
        console.log("抓到銀髮大使活動資料", response.data);
        this.test2 = response.data;
        this.content = response.data[0][2];
        // this.$store.commit("setediteditolderangelinfo", response.data[0]);
      });
    }

    const form = document.getElementById("olderangelform");

    form.addEventListener("submit", e => {
      e.preventDefault();
      const file = form.file.files[0];
      const fr = new FileReader();
      fr.readAsArrayBuffer(file);
      fr.onload = f => {
        const url =
          "https://script.google.com/macros/s/AKfycbx__5o56mSX2boWM74h5ckn32kNwv20aqRR2JD0HRoOPeHRlE_HLkeOo39jkSQ3Inzz/exec"; // <--- Please set the URL of Web Apps.
        const qs = new URLSearchParams({
          filename: form.filename.value || file.name,
          mimeType: file.type
        });
        fetch(`${url}?${qs}`, {
          method: "POST",
          body: JSON.stringify([...new Int8Array(f.target.result)]),
          mode: "no-cors"
        })
          .then(response => {
            console.log("success:", response);
            this.test(form.filename.value);
          })
          .catch(err => {
            console.log("Error:" + err);
          });
      };
    });
  },
  methods: {
    test(filename) {
      axios
        .get(
          "https://script.google.com/macros/s/AKfycbxQxEAwNccWBjqeu9tgVSc9HV0aww4oqC7bGUQDYd06V1wtrgpaZ_kigL9XLjl1vnDhCw/exec",
          {
            params: {
              filename: filename
            }
          }
        )
        .then(res => {
          console.log(res.data);
          this.picurl = res.data;
          alert("上傳成功");
          // let postdata1 = {
          //   datatype: "actpic",
          //   data: res.data
          // };
          // this.$store.commit("setactinfo", postdata1);
        })
        .catch(error => {
          console.error(error);
        });
    },

    catchinputcontent(inputcontent, inputheader, isempty) {
      let postdata = {
        datatype: "type",
        data: inputcontent
      };
      switch (inputheader) {
        case "銀髮大使姓名":
          postdata.datatype = "olderangelname";

          this.checklist[0] = isempty;
          this.$store.commit("setolderangelinfo", postdata);
          break;
        case "大使個人照":
          postdata.datatype = "olderangelpic";
          this.$store.commit("setolderangelinfo", postdata);
          break;
        case "銀髮大使興趣/專長":
          postdata.datatype = "olderangelskill";
          this.$store.commit("setolderangelinfo", postdata);
          break;
      }
      console.log(postdata, isempty);
    },
    upstep() {
      this.$store.commit("minuscreateactindex");
    },
    setfocus() {
      // let olderangelintro = this.$refs.olderangelintro;
      // olderangelintro.style.borderColor="#87DDB1"
    },
    nextstep() {
      if (this.checklist.includes(true)) {
        alert("請填寫完畢");
      } else {
        this.$store.commit("addcreateactindex");
        console.log("改變");
        let postdata = {
          datatype: "olderangeldescribe",
          data: this.content
        };
        this.$store.commit("setolderangelinfo", postdata);
      }
    }
  },
  computed: {
    editeventinfo() {
      let arr = [];
      if (this.$store.state.editeventinfo == "") {
        arr = [];
      } else {
        arr = this.$store.state.editeventinfo;
      }
      return arr;
    },
    olderangelinfo() {
      return this.$store.getters.geteditolderangelinfo;
    }
  },
  components: {
    fillbtn,
    inputoutline
  }
};
</script>


